import React from 'react'
import { withRouter } from 'react-router-dom';
import './details.css'
class Details extends React.Component{
    render(){
        return(
        <div className='details'>
            <img src='https://img.alicdn.com/imgextra/i1/3021068273/O1CN01pwl4co2Az58YovM8q_!!3021068273.jpg_760x760Q50s50.jpg_.webp'></img>
            
            <p className='num'>￥34.00</p>
            <p className='text'>筷子家用实木20双套装家庭装无漆无蜡天然竹子加长火锅筷批发24cm</p>
            <i className='iconfont icon-fanhui' id='fanhui'
            onClick={()=>{
                this.props.history.push('/');
            }}></i>
            <div className='qwe'></div>
            <ul className='buttonbar'>
                <button className='but'>
                    <i className='iconfont icon-dianpu'></i>
                    <p>店铺</p>
                </button>
                <button className='but'>
                    <i className='iconfont icon-kefu'></i>
                    <p>客服</p>
                </button>
                <button className='but'>
                    <i className='iconfont icon-shoucang'></i>
                    <p>收藏</p>
                </button>
                <button className='addcart' 
                // style={{marginLeft:40}}
                >
                    加入购物车
                </button>
    
                <button className='tobuy'>
                    立即购买
                </button>
            </ul>
        </div>
        )
    }
}

export default withRouter(Details)